@tailwind base;
@tailwind components;
@tailwind utilities;

@layer utilities {
    @variants responsive {
      /* Hide scrollbar for Chrome, Safari and Opera */
      .no-scrollbar::-webkit-scrollbar {
          display: none;
      }

      /* Hide scrollbar for IE, Edge and Firefox */
      .no-scrollbar {
          -ms-overflow-style: none;  /* IE and Edge */
          scrollbar-width: none;  /* Firefox */
      }
    }
  }


.literal {
  @apply rounded-tr-lg rounded-bl-lg;
}

.identifier {
  @apply rounded-tr-lg rounded-bl-lg;
}

.draggable {
  @apply cursor-grab hover:bg-zinc-700;
}

.empty-leaf {
  @apply bg-zinc-500;
}

.expression {
  @apply rounded-tr-lg;
}

::-webkit-scrollbar {
    @apply w-1;
}
 
/* Track */
::-webkit-scrollbar-track {
    @apply bg-transparent;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
    background: #888;
    border-radius: 5px;
}
 
/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    @apply bg-zinc-300;
}